import React, { useState, useEffect } from "react";
import PostList from "./PostList";
import { API } from "../api-service";
import useToken from "./useToken";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowLeft, faArrowRight } from "@fortawesome/free-solid-svg-icons";

const HomePage = () => {
  const { token } = useToken();
  const [resources, setResources] = useState([]);
  const [currentResources, setCurrentResources] = useState(null);

  const [postPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageCount, setPageCount] = useState(0);

  useEffect(() => {
    console.log(token);
    API.fetchAllResources(token, setResources);
  }, [token]);

  useEffect(() => {
    const lastPostIndex = currentPage * postPerPage;
    const firstPostIndex = lastPostIndex - postPerPage;
    const newCurrentResources = resources.slice(firstPostIndex, lastPostIndex);

    console.log(newCurrentResources);
    setCurrentResources(newCurrentResources);
    setPageCount(resources.length / postPerPage);
  }, [resources, currentPage]);

  const nextResources = () => {
    const newCurrentPage = currentPage + 1;
    console.log(`newCurrentPage: ${newCurrentPage}`);
    console.log(newCurrentPage > pageCount);

    setCurrentPage(newCurrentPage > pageCount ? currentPage : newCurrentPage);
  };

  const prevResources = () => {
    const newCurrentPage = currentPage - 1;
    console.log(`newCurrentPage: ${newCurrentPage}`);
    console.log(newCurrentPage < 0);

    setCurrentPage(newCurrentPage < 0 ? currentPage : newCurrentPage);
  };

  return (
    <div className="homepage" data-test="homepage">
      <PostList resources={currentResources} />
      <button onClick={prevResources}>
        <FontAwesomeIcon icon={faArrowLeft} />
      </button>
      <button onClick={nextResources}>
        <FontAwesomeIcon icon={faArrowRight} />
      </button>
    </div>
  );
};

export default HomePage;
